// breakpoints
@import "../../styles/breakpoints.scss";

// colors
@import "../../styles/colors.scss";

.container {
  padding: 40px 30px;
  background-image: url("../../../public/images/about-me-background.png");
  background-repeat: repeat;
  background-size: 300px;
  display: flex;
  flex-direction: column;
  gap: 20px;

  @include sm {
    padding: 40px 20px;
  }

  .title {
    font-size: 40px;
    font-weight: 600;
    font-family: "Syncopate", sans-serif;
    font-style: normal;
    margin: 0;
    color: $light-1;

    @include xs {
      font-size: 30px;
      line-height: 30px;
    }

    span {
      color: $green-1;
    }
  }
}
