// breakpoints
@import "../../styles/breakpoints.scss";

// colors
@import "../../styles/colors.scss";

.container {
  padding: 10px;
  border-radius: 5px;
  background: rgba(255, 231, 77, 0.2);
  display: flex;
  gap: 10px;
  align-items: center;

  @include xs {
    flex-direction: column;
    text-align: center;
  }

  svg {
    width: 40px;
    height: 40px;
    min-width: 40px;
  }

  .message {
    font-size: 15px;
    font-weight: 500;
    font-family: "Roboto Mono", monospace;
    font-style: normal;
    color: $light-1;
  }
}
