// breakpoints
@import "../../styles/breakpoints.scss";

// colors
@import "../../styles/colors.scss";

.container {
  padding: 40px 30px;
  background-image: url("../../../public/images/lamp.png");
  background-repeat: no-repeat;
  background-size: 80px;
  background-position: 10% 10%;

  @include sm {
    padding: 40px 20px;
  }

  .content {
    background-image: url("../../../public/images/keyboard.png");
    background-position: bottom right;
    background-repeat: no-repeat;
    background-size: 200px;
    display: flex;
    flex-direction: column;
    gap: 30px;

    .title {
      font-size: 40px;
      font-weight: 600;
      font-family: "Syncopate", sans-serif;
      font-style: normal;
      margin: 0;
      color: $light-1;

      @include xs {
        font-size: 30px;
        line-height: 30px;
      }
    }

    .companies {
      display: flex;
      flex-direction: column;
      gap: 30px;

      .company {
        display: flex;
        flex-direction: column;
        gap: 10px;

        .nameAndIcon {
          display: flex;
          gap: 10px;

          .iconBlock {
            display: flex;
            justify-content: center;
            padding: 10px;
            background: rgba(255, 255, 255, 0.1);
            border-radius: 5px;

            svg {
              width: 40px;
              height: 40px;
            }
          }

          .companyName {
            flex: 1;
            padding: 10px 15px;
            background: rgba(255, 255, 255, 0.1);
            border-radius: 5px;
            display: flex;
            align-items: center;
            font-size: 25px;
            font-weight: 500;
            font-family: "Roboto Mono", monospace;
            font-style: normal;
            color: $light-1;

            span {
              color: $green-1;
            }
          }
        }

        .jobTitleAndDuration {
          display: grid;
          grid-template-columns: auto 1fr;
          gap: 10px;

          @include sm {
            grid-template-columns: 1fr;
          }

          .jobTitle {
            padding: 10px 15px;
            background: rgba(255, 255, 255, 0.1);
            border-radius: 5px;
            display: flex;
            align-items: center;
            font-size: 15px;
            font-weight: 500;
            font-family: "Roboto Mono", monospace;
            font-style: normal;
            color: $light-1;
          }

          .duration {
            padding: 10px 15px;
            background: rgba(255, 255, 255, 0.1);
            border-radius: 5px;
            display: flex;
            align-items: center;
            font-size: 15px;
            font-weight: 500;
            font-family: "Roboto Mono", monospace;
            font-style: normal;
            color: $light-1;
          }
        }
      }
    }
  }
}
