// breakpoints
@import "../../styles/breakpoints.scss";

// colors
@import "../../styles/colors.scss";

.container {
  display: grid;
  grid-template-columns: 1.5fr 1fr;
  gap: 20px;
  padding: 0 20px;

  @include sm {
    grid-template-columns: 100%;
  }

  .infoBlock {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 0;

    .infoContent {
      display: flex;
      gap: 5px;

      .arrowImageBlock {
        display: flex;
        flex: 1;
        padding: 10px 0 0 0;

        .arrowImage {
          height: 100%;
          width: 55px;

          @include md {
            width: 35px;
          }

          @include xs {
            width: 30px;
          }
        }
      }
      .titleAndContentBlock {
        display: flex;
        flex-direction: column;
        gap: 5px;

        .actionsBlock {
          padding-top: 15px;
        }

        h1 {
          font-size: 65px;
          line-height: 65px;
          font-weight: 500;
          font-style: normal;
          margin: 0;

          @include md {
            font-size: 40px;
            line-height: 40px;
          }

          @include xs {
            font-size: 30px;
            line-height: 30px;
          }
        }

        .frontEnd {
          font-family: "Parisienne", sans-serif;
          color: $light-1;
        }

        .developer {
          font-family: "Syncopate", sans-serif;
          color: $green-1;
        }

        button {
          @include xs {
            width: 100%;
            justify-content: center;
          }
        }
      }
    }
  }

  .imageBlock {
    min-height: 280px;
    position: relative;
    padding-bottom: 15px;
    display: flex;
    justify-content: center;

    .imageContainer {
      position: relative;
      width: 100%;
      height: 100%;

      @include sm {
        max-width: 350px;
      }

      @include xs {
        max-width: 280px;
      }

      .backgroundImage {
        width: 100%;
      }
      .userImage {
        width: 70%;
        position: absolute;
        left: 0;
        bottom: 0;
      }
    }
  }
}
