// breakpoints
@import "../../styles/breakpoints.scss";

// colors
@import "../../styles/colors.scss";

.container {
  padding: 10px;
  border-radius: 5px;
  border: 1px solid $green-1;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;

  .title {
    margin: 0;
    font-size: 25px;
    font-weight: 500;
    font-family: "Roboto Mono", monospace;
    font-style: normal;
    color: $green-1;
  }

  .timer {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    gap: 5px;

    .timerBlock {
      padding: 5px;
      border-radius: 5px;
      background: rgba(255, 255, 255, 0.1);
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 5px;

      span {
        font-size: 15px;
        font-weight: 500;
        font-family: "Roboto Mono", monospace;
        font-style: normal;
        color: $green-1;

        @include sm {
          font-size: 8px;
        }
      }

      .value {
        font-size: 17px;
      }
    }
  }
}
