// colors
@import "../../../styles/colors.scss";

.button {
  padding: 10px 32px;
  border: none;
  outline: none;
  border-radius: 24px;
  font-size: 18px;
  font-weight: 500;
  color: $light-1;
  background: $green-1;
  overflow: hidden;
  display: flex;
  align-items: center;
  gap: 10px;

  &:hover {
    cursor: pointer;
    background: $green-2;
  }
}
