// breakpoints
@import "../../styles/breakpoints.scss";

// colors
@import "../../styles/colors.scss";

.container {
  padding: 40px 30px;
  background-image: url("../../../public/images/about-me-background.png");
  background-repeat: repeat;
  background-size: 300px;
  display: flex;
  flex-direction: column;
  gap: 20px;

  @include sm {
    padding: 40px 20px;
  }

  .title {
    font-size: 40px;
    font-weight: 600;
    font-family: "Syncopate", sans-serif;
    font-style: normal;
    margin: 0;
    color: $light-1;

    @include xs {
      font-size: 30px;
      line-height: 30px;
    }
  }

  .skillsBlock {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 10px;

    @include md {
      grid-template-columns: 1fr 1fr;
    }

    @include sm {
      grid-template-columns: 100%;
    }

    .skillCard {
      padding: 10px 15px;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 5px;
      display: grid;
      grid-template-columns: 45px 1fr;
      gap: 15px;
      align-items: center;

      .iconBlock {
        display: flex;

        svg {
          width: 45px;
          height: 45px;
          min-width: 45px;
        }
      }

      .info {
        display: flex;
        flex-direction: column;
        gap: 5px;

        .skillTitle {
          font-size: 20px;
          font-weight: 500;
          font-family: "Roboto Mono", monospace;
          font-style: normal;
          color: $light-1;

          @include xs {
            font-size: 18px;
          }
        }
        .years {
          font-size: 15px;
          font-weight: 500;
          font-family: "Roboto Mono", monospace;
          font-style: normal;
          color: $light-1;
        }
        .hr {
          border-top: 1px solid rgba(255, 255, 255, 0.3);
        }
      }
    }
  }
}
