// breakpoints
@import "../../styles/breakpoints.scss";

// colors
@import "../../styles/colors.scss";

.container {
  display: flex;
  flex-direction: column;
  gap: 10px;

  .aboutMe {
    display: flex;
    gap: 10px;

    @include xs {
      flex-direction: column;
    }

    .avatarBlock {
      display: flex;
      justify-content: center;
      padding: 10px;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 5px;

      .avatar {
        width: 120px;
        height: 120px;
        border-radius: 120px;
        object-fit: contain;
      }
    }

    .information {
      width: 100%;
      display: grid;
      grid-template-columns: 100%;
      grid-template-areas: 50% 50%;
      gap: 10px;

      .name {
        padding: 10px 15px;
        background: rgba(255, 255, 255, 0.1);
        border-radius: 5px;
        display: flex;
        align-items: center;
        font-size: 20px;
        font-weight: 500;
        font-family: "Roboto Mono", monospace;
        font-style: normal;
        color: $light-1;
      }

      .birthDay {
        padding: 10px 15px;
        background: rgba(255, 255, 255, 0.1);
        border-radius: 5px;
        display: flex;
        align-items: center;
        font-size: 20px;
        font-weight: 500;
        font-family: "Roboto Mono", monospace;
        font-style: normal;
        color: $light-1;
      }
    }
  }

  .locationBlock {
    padding: 10px 15px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 5px;
    display: flex;
    align-items: center;
    gap: 15px;

    svg {
      width: 40px;
      height: 40px;
    }

    span {
      font-size: 20px;
      font-weight: 500;
      font-family: "Roboto Mono", monospace;
      font-style: normal;
      color: $light-1;
    }
  }

  .universityBlock {
    padding: 10px 15px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 5px;
    display: flex;
    align-items: center;
    gap: 15px;

    @include xs {
      flex-direction: column;
      text-align: center;
    }

    svg {
      min-width: 40px;
      width: 40px;
      height: 40px;
    }

    .university {
      display: flex;
      flex-direction: column;
      gap: 5px;

      h1 {
        margin: 0;
        font-size: 20px;
        font-weight: 500;
        font-family: "Roboto Mono", monospace;
        font-style: normal;
        color: $light-1;
      }
      h2 {
        margin: 0;
        font-size: 15px;
        font-weight: 500;
        font-family: "Roboto Mono", monospace;
        font-style: normal;
        color: $light-1;
      }
    }
  }
  .contactsBlock {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;

    @include sm {
      grid-template-columns: 100%;
    }

    .contact {
      padding: 10px 15px;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 5px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 10px;

      .contactInfo {
        display: flex;
        align-items: center;
        gap: 15px;
        overflow: hidden;

        svg {
          width: 45px;
          height: 45px;
          min-width: 45px;
        }

        p {
          width: 100%;
          margin: 0;
          font-size: 20px;
          font-weight: 500;
          font-family: "Roboto Mono", monospace;
          font-style: normal;
          color: $light-1;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;

          @include sm {
            font-size: 15px;
          }
        }
      }
    }
  }
}
